import Vue from 'vue'
import Router from 'vue-router'
import Root from '@/components/Root'
import PersonalInfo from '@/components/PersonalInfo'
import Skills from '@/components/Skills'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Root
    },
    {
      path: '/PersonalInfo',
      name: 'PersonalInfo',
      component: PersonalInfo
    },
    {
      path: '/Skills',
      name: 'Skills',
      component: Skills
    },
    {
      path: '/WorkExperience',
      name: 'WorkExperience',
      component: PersonalInfo
    },
    {
      path: '/Education',
      name: 'Education',
      component: PersonalInfo
    },
    {
      path: '/OtherDegrees',
      name: 'OtherDegrees',
      component: PersonalInfo
    },
    {
      path: '/LanguageSkills',
      name: 'LanguageSkills',
      component: PersonalInfo
    },
    {
      path: '/Miscellaneous',
      name: 'Miscellaneous',
      component: PersonalInfo
    }
  ]
})



